import uniRate from '@/components/uni-rate/uni-rate.vue';
import foot from '../component/foot';
export default {
	components: {
		uniRate,
		foot
	},
	props: {
		shopIndex: {
			type: Object,
			default: {}
		},
		commentList: {
			type: Array,
			default: []
		}
	},
	computed: {
		// 店铺照片
		shopImg() {
			if (this.shopIndex.images) {
				return this.shopIndex.images.filter(v => {
					return v.type == 1
				})
			}
		},
		// 经营执照|食品许可
		LicenseList() {
			if (this.shopIndex.images) {
				return this.shopIndex.images.filter(v => {
					return v.type == 2 || v.type == 4
				})
			}
		},
		// 健康证
		cardList() {
			if (this.shopIndex.images) {
				return this.shopIndex.images.filter(v => {
					return v.type == 3
				})
			}
		},
	},
	mounted() {
		const user_id = this.$db.get("userinfo").user_id || '';
		const shop_id = this.shopIndex.id;
		this.$http.postAddBeenShop({
			user_id,
			shop_id
		}, res => {
			if(res.code == 1){
				
			} else {
				this.$common.errorToShow(res.msg);
			}
		})
	},
	methods: {
		isShowFullText(v) {
			return v.split("").length >= 56
		},
		previewImage(v, e) {
			let list = [];
			const current = e.currentTarget.dataset.src;
			v.map(el => {
				list.push(el.image_url)
			})
			uni.previewImage({
				urls: list,
				current: current,
			})
		},
		_changeLike(item, bl, index) {
			this.$emit("changeLike", {
				item,
				bl,
				index
			})
		},
		_changeFullText(e) {
			this.$emit("changeFullText", {
				e
			})
		},
		_switchPostComments() {
			this.$emit("switchPostComments");
		},
	}
}
